a {
  text-decoration: none;
  color: black;
}
body{
  width:100vw;
  margin-left: calc(-50vw + 50%);
}
#navibar{
  font-size:20px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  padding-top: 30px;
  margin-left: -6px;
  margin-top: -6px;
}
#navibar a{
  color: #401E20;
}
#navibar #menu{
  margin-right:30px;
}
#navibar #rightbar{
  border-right: #401E20 1px solid;
  padding-right: 5px;
}
#navibar button{
  background-color:transparent;
  border-radius: 5px;
  border: #F52334 1px solid; 
  color: #F52334;
}